/* src/Clock.css */
.clock-container {
    background-color: #282c34;
    color: white;
    padding: 20px;
    border-radius: 10px;
    margin-top: 20px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    text-align: center;
  }
  
  .clock-time {
    font-size: 2em;
    font-weight: bold;
  }
  